.Cell {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 120px;
  font-size: 110px;
  text-align: center;
  color: rgb(179, 174, 174);

  cursor: pointer;
  border: none;
  border-right: 5px solid #b160ca;
  border-bottom: 5px solid #b160ca;
  box-sizing: border-box;
  background-color: #434261;
}
